"use client";
import React, { useEffect, useState } from "react";
import { navigate } from "gatsby";
import { FiArrowLeft, FiDownload, FiStar, FiMapPin, FiCalendar, FiAward, FiMail, FiPhone, FiGlobe, FiFileText, FiExternalLink, FiYoutube, FiFacebook, FiMessageCircle } from "react-icons/fi";
import { Honoree } from "../componets/Honorees/data";
import { findHonoreeBySlug } from "../componets/Honorees/slugUtils";
import { useLanguage } from "../context/LanguageContext";
import { getContent } from "../content/content";
import Header from "../componets/header/Header";
import Footer from "../componets/footer/Footer";
import "../componets/styles/profile.css";

interface HonoreeProfileProps {
  pageContext?: {
    slug: string;
    honoree: Honoree;
  };
}

const HonoreeProfile: React.FC<HonoreeProfileProps> = ({ pageContext }) => {
  const { lang } = useLanguage();
  const content = getContent(lang);
  const [honoree, setHonoree] = useState<Honoree | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (pageContext?.honoree) {
      setHonoree(pageContext.honoree);
      setLoading(false);
    } else {
      const urlParams = new URLSearchParams(window.location.search);
      const pathSlug = window.location.pathname.split('/').pop() || urlParams.get('slug') || '';
      
      const foundHonoree = findHonoreeBySlug(pathSlug);
      setHonoree(foundHonoree || null);
      setLoading(false);
    }
  }, [pageContext]);

  if (loading) {
    return (
      <div>
        <Header />
        <div className="loading-container">
          <div className="loading-text">{content.honoreeProfile.section.loading}</div>
        </div>
        <Footer />
      </div>
    );
  }

  if (!honoree) {
    return (
      <div>
        <Header />
        <div className="not-found-container">
          <div className="not-found-content">
            <h1 className="not-found-title">{content.honoreeProfile.section.notFound.title}</h1>
            <button 
              onClick={() => navigate('/honorees')}
              className="back-button"
            >
              {content.honoreeProfile.section.notFound.backButton}
            </button>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  const breadcrumbs = [
    { label: content.pages.globals.section.headerContent.navLinks[0].text, href: "/" },
    { label: content.pages.globals.section.headerContent.navLinks[3].text, href: "/honorees" },
    { label: honoree.name, href: "" }
  ];

  return (
    <div className="honoree-profile">
      <Header />
      
      {/* Navigation */}
      <div className="container">
        <nav className="breadcrumb">
          {breadcrumbs.map((crumb, index) => (
            <React.Fragment key={index}>
              {crumb.href ? (
                <button 
                  onClick={() => navigate(crumb.href)}
                  className="breadcrumb-link"
                >
                  {crumb.label}
                </button>
              ) : (
                <span className="breadcrumb-current">{crumb.label}</span>
              )}
              {index < breadcrumbs.length - 1 && <span className="breadcrumb-separator">›</span>}
            </React.Fragment>
          ))}
        </nav>

        <button
          onClick={() => navigate('/honorees')}
          className="back-link"
        >
          <FiArrowLeft className="back-icon" /> 
          {content.honoreeProfile.section.backToHonorees}
        </button>
      </div>

      {/* Hero Section */}
      <div className="hero-wrapper">
        <div className="hero-card">
          <div className="hero-header">
            <div className="hero-content">
              {/* Main Info */}
              <div className="hero-info">
                <h1 className="hero-name">{honoree.name}</h1>
                <div className="hero-meta">
                  <div className="category-badge">
                    {honoree.category}
                  </div>
                  <div className="star-rating">
                    {[...Array(5)].map((_, i) => (
                      <FiStar key={i} className="star" />
                    ))}
                  </div>
                </div>
                <p className="hero-description">{honoree.fieldOfEstablishment}</p>
              </div>
              
              {/* Avatar */}
          
            </div>
            
            {/* Key Figures Grid */}
            <div className="hero-stats">
              <div className="stat-item">
                <div className="stat-number">127</div>
                <div className="stat-label">{content.honoreeProfile.section.stats.patents}</div>
              </div>
              <div className="stat-item">
                <div className="stat-number">45</div>
                <div className="stat-label">{content.honoreeProfile.section.stats.projects}</div>
              </div>
              <div className="stat-item">
                <div className="stat-number">15</div>
                <div className="stat-label">{content.honoreeProfile.section.stats.initiatives}</div>
              </div>
              <div className="stat-item">
                <div className="stat-number">200+</div>
                <div className="stat-label">{content.honoreeProfile.section.stats.students}</div>
              </div>
            </div>
          </div>
          
          {/* Certificate Badge */}
          <div className="certificate-section">
            <div className="certificate-content">
              <div className="certificate-info">
                <div className="certificate-icon">
                  <FiAward className="award-icon" />
                </div>
                <div>
                  <h3 className="certificate-title">{content.honoreeProfile.section.certificate.title}</h3>
                  <p className="certificate-details">{content.honoreeProfile.section.certificate.code} <span className="certificate-code">Wt-KL-2024-091</span> • {content.honoreeProfile.section.certificate.date} 14/11/2024</p>
                </div>
              </div>
              <div className="status-badge">
                <div className="status-dot"></div>
                <span className="status-text">{content.honoreeProfile.section.certificate.status}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="container" style={{ marginTop: '3rem' }}>
        <div className="content-grid">
          {/* Left Column - Main Content */}
          <div className="main-content">
            {/* Achievements Timeline */}
            <div className="content-card">
              <div className="card-header">
                <h2 className="card-title">
                  <div className="title-icon">
                    <FiAward className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.achievements}
                </h2>
              </div>
              <div className="card-body">
                <div className="timeline-item">
                  <div className="timeline-line"></div>
                  <div className="timeline-content">
                    <div className="timeline-icon achievement">
                      <FiAward className="icon" />
                    </div>
                    <div className="timeline-details">
                      <div className="timeline-header">
                        <h3 className="timeline-title">{content.honoreeProfile.section.achievements.record.title}</h3>
                        <span className="timeline-year">{content.honoreeProfile.section.achievements.record.year}</span>
                      </div>
                      <p className="timeline-description">{content.honoreeProfile.section.achievements.record.description}</p>
                    </div>
                  </div>
                </div>
                
                <div className="timeline-item">
                  <div className="timeline-line"></div>
                  <div className="timeline-content">
                    <div className="timeline-icon project">
                      <FiStar className="icon" />
                    </div>
                    <div className="timeline-details">
                      <div className="timeline-header">
                        <h3 className="timeline-title">{content.honoreeProfile.section.achievements.project.title}</h3>
                        <span className="timeline-year">{content.honoreeProfile.section.achievements.project.year}</span>
                      </div>
                      <p className="timeline-description">{content.honoreeProfile.section.achievements.project.description}</p>
                    </div>
                  </div>
                </div>
                
                <div className="timeline-item">
                  <div className="timeline-content">
                    <div className="timeline-icon collaboration">
                      <FiGlobe className="icon" />
                    </div>
                    <div className="timeline-details">
                      <div className="timeline-header">
                        <h3 className="timeline-title">{content.honoreeProfile.section.achievements.collaboration.title}</h3>
                        <span className="timeline-year">{content.honoreeProfile.section.achievements.collaboration.year}</span>
                      </div>
                      <p className="timeline-description">{content.honoreeProfile.section.achievements.collaboration.description}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Press News */}
            <div className="content-card">
              <div className="card-header press">
                <h2 className="card-title">
                  <div className="title-icon press">
                    <FiFileText className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.news}
                </h2>
              </div>
              <div className="card-body">
                <article className="news-article">
                  <div className="article-content">
                    <div className="article-icon news">
                      <FiFileText className="icon" />
                    </div>
                    <div className="article-details">
                      <div className="article-meta">
                        <span className="article-date">{content.honoreeProfile.section.news.article1.date}</span>
                        <FiExternalLink className="external-icon" />
                      </div>
                      <h3 className="article-title">
                        {content.honoreeProfile.section.news.article1.title}
                      </h3>
                      <p className="article-description">{content.honoreeProfile.section.news.article1.description}</p>
                    </div>
                  </div>
                </article>
                
                <article className="news-article award">
                  <div className="article-content">
                    <div className="article-icon award">
                      <FiAward className="icon" />
                    </div>
                    <div className="article-details">
                      <div className="article-meta">
                        <span className="article-date">{content.honoreeProfile.section.news.article2.date}</span>
                        <FiExternalLink className="external-icon" />
                      </div>
                      <h3 className="article-title">
                        {content.honoreeProfile.section.news.article2.title}
                      </h3>
                      <p className="article-description">{content.honoreeProfile.section.news.article2.description}</p>
                    </div>
                  </div>
                </article>
              </div>
            </div>

            {/* Related Keywords */}
            <div className="content-card">
              <div className="card-header keywords">
                <h2 className="card-title">
                  <div className="title-icon keywords">
                    <FiStar className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.keywords}
                </h2>
              </div>
              <div className="card-body">
                <div className="keywords-container">
                  {(lang === "en" ? ['Nanomaterials', 'Medical Technology', 'Patents', 'Scientific Research', 'International Cooperation', 'Training', 'Sustainable Development'] : ['Vật liệu nano', 'Công nghệ y tế', 'Sáng chế', 'Nghiên cứu khoa học', 'Hợp tác quốc tế', 'Đào tạo', 'Phát triển bền vững']).map((tag, index) => (
                    <span key={index} className="keyword-tag">
                      #{tag}
                    </span>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Right Column - Sidebar */}
          <div className="sidebar">
            {/* QR Code Box */}
            <div className="sidebar-card">
              <div className="card-header">
                <h3 className="card-title">
                  <div className="title-icon">
                    <FiDownload className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.qrCode}
                </h3>
              </div>
              <div className="card-body">
                <div className="qr-code">
                  <img 
                    src="https://ik.imagekit.io/dloitl7fd8/image.png?updatedAt=1760567040537" 
                    alt="QR Code"
                  />
                </div>
                <p className="qr-description">{content.honoreeProfile.section.qr.description}</p>
                <button className="download-button">
                  <FiDownload className="button-icon" />
                  {content.honoreeProfile.section.qr.download}
                </button>
              </div>
            </div>

            {/* Contact Info Box */}
            <div className="sidebar-card">
              <div className="card-header contact">
                <h3 className="card-title">
                  <div className="title-icon contact">
                    <FiMail className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.contact}
                </h3>
              </div>
              <div className="card-body">
                <div className="contact-item">
                  <div className="contact-icon email">
                    <FiMail className="icon" />
                  </div>
                  <div className="contact-details">
                    <div className="contact-label">{content.honoreeProfile.section.contact.email}</div>
                    <div className="contact-value">nguyen@ministry.edu.vn</div>
                  </div>
                </div>
                <div className="contact-item">
                  <div className="contact-icon phone">
                    <FiPhone className="icon" />
                  </div>
                  <div className="contact-details">
                    <div className="contact-label">{content.honoreeProfile.section.contact.phone}</div>
                    <div className="contact-value">+84 24 3756 xxxx</div>
                  </div>
                </div>
                <div className="contact-item">
                  <div className="contact-icon address">
                    <FiMapPin className="icon" />
                  </div>
                  <div className="contact-details">
                    <div className="contact-label">{content.honoreeProfile.section.contact.address}</div>
                    <div className="contact-value">{lang === "en" ? "123 Science Street, Cau Giay, Hanoi" : "123 Đường Khoa học, Cầu Giấy, Hà Nội"}</div>
                  </div>
                </div>
              </div>
            </div>

            {/* Social Links */}
            <div className="sidebar-card">
              <div className="card-header social">
                <h3 className="card-title">
                  <div className="title-icon social">
                    <FiMessageCircle className="icon" />
                  </div>
                  {content.honoreeProfile.section.sections.social}
                </h3>
              </div>
              <div className="card-body">
                <div className="social-grid">
                  <button className="social-button youtube">
                    <FiYoutube className="social-icon" />
                  </button>
                  <button className="social-button facebook">
                    <FiFacebook className="social-icon" />
                  </button>
                  <button className="social-button message">
                    <FiMessageCircle className="social-icon" />
                  </button>
                  <button className="social-button phone">
                    <FiPhone className="social-icon" />
                  </button>
                </div>
              </div>
            </div>

            {/* Statistics Box */}
          
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default HonoreeProfile;

export const Head = ({ pageContext }: HonoreeProfileProps) => {
  const honoree = pageContext?.honoree;
  
  return (
    <>
      <title>{honoree ? `${honoree.name} - Tinh Hoa Việt` : 'Honoree Profile - Tinh Hoa Việt'}</title>
      <meta name="description" content={honoree ? `Learn about ${honoree.name}, recognized for excellence in ${honoree.fieldOfEstablishment}` : 'View detailed profile of Vietnamese quintessence honoree'} />
    </>
  );
};